import React, { useState, useEffect } from 'react';
import styled from 'styled-components';
import ReactDragListView from 'react-drag-listview';

import { MdDeleteForever, MdReorder, MdEdit } from 'react-icons/md';

import usePrevious from '../../hooks/usePrevious';

import * as Utils from '../../utils';

const Container = styled.div`
	display: flex;
	flex-direction: column;
	justify-content: space-around;
	position: absolute;
	width: 400px;
	height: 200px;
	left: 30px;
	bottom: 55px;
	box-shadow: 4px 4px 8px 0 rgba(0, 0, 0, 0.2);
`;

const StyledContainer = styled.div`
	width: 100%;
	height: 100%;
	background-color: #fff;
	overflow-y: scroll;
	border-top-left-radius: 2px;
	border-top-right-radius: 2px;
`;

const List = styled.ol`
	list-style-type: none;
	margin: 10px 0 0 0;
	padding: 0;
	display: flex;
	flex-direction: column;
	align-items: center;
`;

const StyledItemCoord = styled.li`
	width: 338px;
	margin-bottom: 5px;
	padding: 5px 10px;
	border: 1px solid #bbb;
	display: flex;
	flex-direction: row;
	justify-content: space-between;
	align-items: center;
	background-color: ${props => (props.created ? '#26A598' : '#fff')};
	color: ${props => (props.created ? '#fff' : '#222')};
	transition: all 0.5s;
	font-weight: 600;
	font-size: 0.8em;
	font-family: 'Roboto';
	user-select: none;
	cursor: pointer;
`;

const ContainerAcoes = styled.div`
	display: flex;
	flex-direction: row;
	justify-content: center;
	align-items: center;
`;

const IconButton = styled.button`
	text-align: center;
	height: 100%;
	padding: 0 2px;
	border: none;
	background: none;
	font-size: 1.3em;
	color: inherit;
	display: inline-flex;
	flex-direction: column;
	justify-content: center;
	cursor: pointer;
	&:focus {
		outline: 0;
	}
`;

const ContainerDescricao = styled.div`
	padding: 0 10px;
	text-align: center;
`;

const ItemCoord = props => {
	const [created, setCreated] = useState(true);

	useEffect(() => {
		setTimeout(() => {
			if (created) setCreated(false);
		}, 200);
	}, [created]);

	return (
		<StyledItemCoord created={created}>
			<IconButton style={{ cursor: 'move' }} id="btn-reorder">
				<MdReorder />
			</IconButton>
			<ContainerDescricao onClick={props.onClick}>
				{Utils.getLetra(props.index + 1)} - {props.coord.address}
			</ContainerDescricao>
			<ContainerAcoes>
				<IconButton onClick={() => props.onEdit(props.index)}>
					<MdEdit />
				</IconButton>
				<IconButton onClick={() => props.onDelete()}>
					<MdDeleteForever />
				</IconButton>
			</ContainerAcoes>
		</StyledItemCoord>
	);
};

const ContainerCoords = ({ coords, onDelete, onEdit, onReorder, onItemClick, children }) => {
	var coordsEnd;

	const prevCoords = usePrevious(coords);

	useEffect(() => {
		if (prevCoords && coords && coords.length > prevCoords.length) {
			coordsEnd.scrollIntoView({ behavior: 'smooth' });
		}
	}, [coords, prevCoords, coordsEnd]);

	const dragProps = {
		onDragEnd(fromIndex, toIndex) {
			const newCoords = coords.filter((coord, index) => index !== fromIndex);
			newCoords.splice(toIndex, 0, coords[fromIndex]);
			onReorder(newCoords);
		},
		nodeSelector: 'li',
		handleSelector: '#btn-reorder',
	};

	return (
		<Container>
			<StyledContainer>
				<ReactDragListView {...dragProps}>
					<List>
						{coords.map((coord, index) => (
							<ItemCoord
								onClick={() => onItemClick(index)}
								key={index}
								index={index}
								coord={coord}
								onEdit={() => onEdit(index)}
								onDelete={() => onDelete(index)}
							/>
						))}
					</List>
					<div style={{ float: 'left', clear: 'both' }} ref={el => (coordsEnd = el)}></div>
				</ReactDragListView>
			</StyledContainer>
			{children}
		</Container>
	);
};

export default ContainerCoords;
