import React from 'react';
import PlacesAutocomplete, { geocodeByAddress, getLatLng } from 'react-places-autocomplete';
import styled from 'styled-components';

const Input = styled.input`
	width: 100%;
	padding: 12px 20px;
	font-family: 'Roboto';
	border-radius: 2px;
	box-shadow: 3px ${props => (props.items ? '0' : '4px')} 8px 0 rgb(0, 0, 0, 0.2);
	border: 1px solid #ccc;
`;

const ContainerSuggestions = styled.div`
	width: 100%;
	background-color: #fff;
	box-shadow: 3px ${props => (props.items ? '4px' : '0')} 8px 0 rgb(0, 0, 0, 0.2);
`;

const ItemSuggestion = styled.div`
	padding: 15px 20px;
	font-size: 0.8em;
	font-family: 'Roboto';
	cursor: pointer;
	background-color: ${props => (props.active ? '#f0f0f0' : '#fff')};
	transition: all 0.1s;
`;

class LocationSearchInput extends React.Component {
	constructor(props) {
		super(props);
		this.onPlaceSelected = props.onPlaceSelected ? props.onPlaceSelected : () => {};
		this.state = { address: '' };
	}

	handleChange = address => {
		this.setState({ address });
	};

	handleSelect = address => {
		geocodeByAddress(address)
			.then(results => getLatLng(results[0]))
			.then(latLng => {
				this.onPlaceSelected({ ...latLng, address: address });
				this.setState({ address: '' });
			})
			.catch(error => console.error(error));
	};

	render() {
		return (
			<PlacesAutocomplete
				highlightFirstSuggestion
				value={this.state.address}
				onChange={this.handleChange}
				onSelect={this.handleSelect}
			>
				{({ getInputProps, suggestions, getSuggestionItemProps, loading }) => (
					<div>
						<Input
							items={suggestions.length > 0}
							{...getInputProps({ placeholder: 'Procurar endereços, cidades, estados, locais...' })}
						/>
						<ContainerSuggestions items={suggestions.length > 0}>
							{loading && <ItemSuggestion>Loading...</ItemSuggestion>}
							{suggestions.map(suggestion => (
								<ItemSuggestion active={suggestion.active} {...getSuggestionItemProps(suggestion)}>
									{suggestion.description}
								</ItemSuggestion>
							))}
						</ContainerSuggestions>
					</div>
				)}
			</PlacesAutocomplete>
		);
	}
}

export default LocationSearchInput;
