import React, { useState, useEffect } from 'react';
import styled from 'styled-components';

const Button = styled.button`
	background-color: #fff;
	border: 1px solid #222;
	border-radius: 1px;
	padding: 7px 15px;
	margin-right: 15px;
	font-weight: 900;
	font-family: 'Roboto';
	transition: all 0.1s;

	&:hover {
		background-color: #222;
		color: #fff;
	}
`;

const Body = styled.div`
	height: 100%;
	width: 100%;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
`;

const Container = styled.div`
	width: 500px;
	height: 200px;
	background-color: #fff;
	border-radius: 2px;
	box-shadow: 4px 4px 8px 0 rgba(0, 0, 0, 0.2);
	font-family: 'Roboto';
	display: grid;
	grid-template-rows: 60px auto 45px;
`;

const Header = styled.div`
	border-bottom: 1px solid #aaa;
	line-height: 60px;
	text-align: center;
	font-size: 1.4em;
`;

const Bottom = styled.div`
	border-bottom: none;
	border-top: 1px solid #aaa;
	font-size: 1em;
	display: flex;
	flex-direction: row;
	justify-content: flex-end;
	align-items: center;
`;

const Input = styled.input`
	width: 80%;
	padding: 12px 20px;
	font-family: 'Roboto';
	border-radius: 2px;
	border: 1px solid #ccc;
`;

const MarcadorEdit = ({ item, onCancel, onConfirm }) => {
	var descricaoInput;
	const [descricao, setDescricao] = useState('');
	const handleConfirmar = () => onConfirm({ ...item, address: descricao });
	useEffect(() => setDescricao(item.address), [item]);
	useEffect(() => {
		descricaoInput.focus();
		descricaoInput.setSelectionRange(0, descricaoInput.value.length);
	}, [descricaoInput]);
	return (
		<Container>
			<Header>Alterar Marcador</Header>
			<Body>
				<Input
					ref={(input) => { descricaoInput = input; }}
					defaultValue={descricao}
					onChange={event => setDescricao(event.target.value)}
					type="text"
					placeholder="Descrição"
				></Input>
			</Body>
			<Bottom>
				<Button onClick={onCancel}>CANCELAR</Button>
				<Button onClick={handleConfirmar}>CONFIRMAR</Button>
			</Bottom>
		</Container>
	);
};

export default MarcadorEdit;
