function distance(position1, position2) {
	var deg2rad = function (deg) { return deg * (Math.PI / 180); },
			R = 6371,
			dLat = deg2rad(position2.lat - position1.lat),
			dLng = deg2rad(position2.lng - position1.lng),
			a = Math.sin(dLat / 2) * Math.sin(dLat / 2)
					+ Math.cos(deg2rad(position1.lat))
					* Math.cos(deg2rad(position1.lat))
					* Math.sin(dLng / 2) * Math.sin(dLng / 2),
			c = 2 * Math.atan2(Math.sqrt(a), Math.sqrt(1 - a));
		return ((R * c *1000).toFixed());
}

//-- Define radius function
const toRad = function (value) {
	return value * Math.PI / 180;
}

//-- Define degrees function
const toDeg = function (value) {
	return value * (180 / Math.PI);
}

//-- Define middle point function
function midPoint(position1, position2) {

	let lat1 = position1.lat;
	let lng1 = position1.lng;
	let lat2 = position2.lat;
	let lng2 = position2.lng;

	//-- Longitude difference
	var dLng = toRad(lng2 - lng1);

	//-- Convert to radians
	lat1 = toRad(lat1);
	lat2 = toRad(lat2);
	lng1 = toRad(lng1);

	var bX = Math.cos(lat2) * Math.cos(dLng);
	var bY = Math.cos(lat2) * Math.sin(dLng);
	var lat3 = Math.atan2(Math.sin(lat1) + Math.sin(lat2), Math.sqrt((Math.cos(lat1) + bX) * (Math.cos(lat1) + bX) + bY * bY));
	var lng3 = lng1 + Math.atan2(bY, Math.cos(lat1) + bX);

	//-- Return result
	return { lat: toDeg(lat3), lng: toDeg(lng3) };
}

const dividirEmDistanciasMenores = (elements, maxDistance) => {
	let newElements = [ ...elements ];
	for (let i = 0; i < elements.length; i++) {
		if (elements[i + 1]) {
			const coord = elements[i];
			const distancia = distance(coord, elements[i + 1]);
			if (distancia > maxDistance) {
				const middlePoint = midPoint(coord, elements[i + 1]);
				newElements.splice(i + 1, 0, middlePoint);
				return dividirEmDistanciasMenores(newElements, maxDistance);
			}
		}
	}
	return newElements;		
}

const getLetra = (number) => {
	const letras = 'ABCDEFGHIJKLMNOPQRSTUVWXYZ';
	return letras.split('')[number - 1];
}

export { 
	distance, 
	midPoint,
	dividirEmDistanciasMenores,
	getLetra
};